// import { useState, useEffect } from "react";

// Use React Hooks for incrementing, decrementing nd resetting the value of the counter.
// Also, print the value of count on the console by using useEffect hook.

import { useState, useEffect } from "react";

function Counter() {
  var [counter, setCounter] = useState(0);

  function handlingIncreaseCount() {
    setCounter(++counter);
  }
  useEffect(() => {
    console.log(counter);
  });

  return (
    <>
      <p>Count value is:{counter} </p>
      <button onClick={() => setCounter(0)}>Reset</button>
      <button onClick={handlingIncreaseCount}>Plus (+)</button>
      <button onClick={() => setCounter(--counter)}>Minus (-)</button>
    </>
  );
}

export default Counter;
